.app {
  padding: 0 24px;
}

.tab-selector {
  margin-top: 32px;
  border-bottom: 1px solid #0353a4;
  margin-left: -24px;
  margin-right: -24px;
  padding-left: 24px;
}

.tab-selector p {
  margin: 0;
}

.tab-selector button {
  cursor: pointer;
  border-radius: 0;
  background: transparent;
  border: none;
  padding: 16px 8px;
  font-size: 20px;
}

.tab-selector button:hover {
  background: #b9d6f2;
}

.tab-selector button.active {
  background: #0353a4;
  color: white;
}
